.inputBox{
    width: 300px;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
}

.label{
    margin-bottom: 10px;
    font-weight: 700;
}

.input{
    font-size: 17px;
    transition: border 300px ease;
}

.input:hover{
    border: 2px solid rgb(69, 69, 69);
}

.buttonBox{
    text-align: center;
}

.addButton{
    text-decoration: none;
    font-family: roboto, sens-serif;
    font-weight: 700;
    font-size: 15px;
    cursor: pointer;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    color: #000;
    background-color: #d4cece;
    box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);
    transition: background-color 300ms ease, color 300ms ease;
}

.addButton:hover,
.addButton:focus{
    background-color: rgb(69, 69, 69);
    color: rgb(248, 248, 248);
}

.required{
    color: rgb(252, 7, 7);
}